.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
#i1{
  width: 300px;
  height: 300px;
  animation: myfirst 11.5s  infinite running linear 0s;
  
  
}

#i1:hover{
  animation: myfirs 3.5s  infinite running linear 0s;
  width: 300px;
  height: 300px;
 
}
@keyframes  myfirs {
  100%{transform: rotatez(360deg);}
}

@keyframes  myfirst {
  100%{transform: rotatez(360deg);}
}

 

.App{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url(bg3.jpg);
  background-size: 100% ;
  background-position: center center;
  background-repeat: no-repeat;

}
.a5{
  width: 520px;
  border: 1px solid white;
  background: transparent;
  backdrop-filter: blur(5px);
  color: antiquewhite;
  font-weight: bolder;
  border-radius: 10px;
  box-shadow: 0px 0px 7px aliceblue;
  padding-top: 2%;
  padding-bottom: 2%;


}

.a4{
  margin-right: 5%;
}
#a2{
width: 50% ;
height: 40px;
margin-top: 3%;
border-radius: 10px;
background-color:transparent;
border: 2px solid whitesmoke;
font-size: medium;
font-weight: bold;
padding-left: 3%;
color: white;
}
#a3{
  height: 40px;
  width: 15%;
  border-radius: 10px;
  border-color: transparent;
  background-color:peachpuff;
  margin-left: 0.5%;
  font-size: medium;
  font-weight:400;
  padding-left: 3%;
  
}
.a1{
  margin-top: 2.5%;
}
#load{
  width: 100vw;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  

}
#loadi{
  height: 80px;
  width: 80px;
  animation: loading 0.5s linear 0s 50 ;
}
@keyframes loading {
  100%{transform: rotatez(360deg);}
}

@media only screen and (max-width: 640px) {
  *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  body{
    display: flex;
    align-items: center;
    justify-content: center;
  
  
  }
  .App{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
   
    background: linear-gradient(109.6deg, rgb(36, 45, 57) 11.2%, rgb(16, 37, 60) 51.2%, rgb(0, 0, 0) 98.6%);;
   
  
  }
  .a4{
  margin-top: -8%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 5%;
  
  }
  .a5{
  width: 90vw;
  height: 50vh;
  padding-top: 5%;
  margin-top: 3%;
  background-color:rgb(3, 3, 18);

  border: 1px solid ;
  border-color: aqua cyan;
  box-shadow: 0px 0px 5px cyan;
  backdrop-filter: blur(40%);

  }
  #i1{
    height: 130px;
    width: 130px;
  }
  #i1:hover{
    animation: myfirs 1.5s  infinite running linear 0s;
    width: 130px;
    height: 130px;
   
  }
  .a1{
    margin-top: 15%;
    
  }
  #a2{
    width: 70%;
    padding-left: 3%;
    border: transparent;
    border-bottom: 3px solid cyan;
    background-color: transparent;
    color: whitesmoke;
    
  }
  
  #a3{
    width:20% ;
    padding-left: 1%;
    border: 2px solid cyan;
    background-color: transparent;
    color: rgb(255, 255, 255);
    
    
  }
  #a6{
    margin-bottom: 10%;
    margin-top: 10%;
    color: whitesmoke;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
